@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans+Extra+Condensed:ital,wght@1,700;1,800&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans TC", sans-serif;
  font-weight: 400;
  font-size: 18px;
  position: relative;
  letter-spacing: 0.055em;
  color: #303036;
}

h1 {
  font-size: 36px;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

button {
  border: none;
}

button:active,
button:focus,
input:focus,
input:active {
  box-shadow: none;
  outline: none;
}
@media screen and (max-width: 768px) {
  * {
    font-size: 16px;
  }
  h1 {
    font-size: 28px;
  }
}
@media screen and (max-width: 480px) {
  * {
    font-size: 14px;
  }
  h1 {
    font-size: 22px;
  }
}

header{
    display: flex;
    height: 70px;
    align-items: center;
    padding: 0 30px;
    position: fixed;
    z-index: 100;
}
.header-logo{
    font-style: italic;
    font-family: 'Fira Sans Extra Condensed', sans-serif;
    font-weight: bold;
    border-bottom: 4px solid #303036;
    display: inline-block;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
@media screen and (max-width: 480px) {
    header{
        padding: 0 25px;
    }
}
.navigation{
    width: 100%;
    height: 100vh;
}
.navigation nav{
    position: absolute;
    background-color: rgba(0,0,0,0.9);
    width: 100%;
    height: 100vh;
}
.home {
  /* height: calc(100vh - 70px); */
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.home-name {
  font-weight: bold;
  font-size: 48px;
  text-align: center;
  letter-spacing: 0.075em;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.home-school {
  text-align: center;
  margin: 25px 0 30px;
}
.home ul {
  width: 240px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
}
.home img {
  width: 90%;
}
.home-year {
  position: absolute;
  display: flex;
  align-items: center;
  transform: rotate(90deg);
  margin-bottom: 100px;
  left: 0px;
}
.home-year-line {
  width: 5px;
  height: 2px;
  background-color: #303036;
}
.home-year-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  text-align: center;
  margin: 0 10px;
  letter-spacing: 3px;
}
@media screen and (max-width: 768px) {
  .home-name {
    font-size: 36px;
  }
  .home-school {
    margin: 12px 0 18px;
  }
  .home ul {
    width: 220px;
  }
  .home img {
    width: 80%;
  }
}
@media screen and (max-width: 480px) {
    .home-name {
      font-size: 30px;
    }
    .home-school {
      margin: 12px 0 18px;
    }
    .home ul {
      width: 170px;
    }
    .home img {
      width: 60%;
    }
    .home-year {
      left: -10px;
    }
}
@media screen and (max-width: 380px) {
    .home-school {
      margin: 15px 0 20px;
    }
    .home-year {
      display: none;
    }
}

.scroll {
  position: absolute;
  bottom: 50px;
  width: 93px;
  overflow: hidden;
}
.scroll-text {
  font-family: Roboto;
  text-align: center;
  line-height: 30px;
  letter-spacing: 5px;
  -webkit-filter: drop-shadow(2px 1px 1px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(2px 1px 1px rgba(0, 0, 0, 0.25));
}
.scroll-line {
  width: 100%;
  height: 3px;
  background-color: #fdd502;
  -webkit-animation: scroll-line 1.5s ease-in-out infinite both;
          animation: scroll-line 1.5s ease-in-out infinite both;
}
@-webkit-keyframes scroll-line {
  0%,
  5% {
    width: 0px;
  }
  33%,
  66% {
    left: 0px;
    width: 100%;
  }
  95%,
  100% {
    left: 100%;
  }
}
@keyframes scroll-line {
  0%,
  5% {
    width: 0px;
  }
  33%,
  66% {
    left: 0px;
    width: 100%;
  }
  95%,
  100% {
    left: 100%;
  }
}

@media screen and (max-width: 768px) {
  .scroll {
    width: 88px;
  }
  .scroll-text {
    line-height: 25px;
  }
}
@media screen and (max-width: 480px) {
  .scroll {
    width: 80px;
  }
  .scroll-text {
    line-height: 22px;
  }
}

.work1 {
  overflow: hidden;
  display: flex;
  width: 100%;
  min-height: 720px;
}
.work1-main {
  width: 100%;
}
.work1-main-anim-hide {
  -webkit-animation-name: relativemove;
          animation-name: relativemove;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.work1-main-anim-show {
  -webkit-animation-name: relativemove2;
          animation-name: relativemove2;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  animation-direction: reverse;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

@-webkit-keyframes relativemove {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes relativemove {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

@-webkit-keyframes relativemove2 {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes relativemove2 {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

.work1-main-bg {
  position: absolute;
  width: 100%;
  height: 100%;
}
.work1-main-img {
  width: 100%;
  min-height: 720px;
  min-width: 1280px;
}

.work1-main-img-l {
  display: block;
}
.work1-main-img-m {
  display: none;
}
.work1-main-content-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  right: 7vw;
  bottom: 100px;
  width: 465px;
  z-index: 1;
}

.work1-text-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 10px;
}
.work1 .work1-h3 {
  font-size: 36px;
  font-weight: bold;
  text-align: right;
  letter-spacing: 0.075em;
  color: #303036;
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.work1-h3-2 {
  padding-left: 15px;
}
.work1 .work1-line {
  margin-top: 5px;
  width: 5px;
  height: 36px;
  background-color: #303036;
}
.work1-main p {
  text-align: right;
  letter-spacing: 0.075em;
  font-weight: bold;
  color: #303036;
}
.work1-main-tag-container {
  display: grid;
  grid-gap: 12px;
  grid-auto-flow: column;
  margin-bottom: 20px;
}
.work1-main-button-container {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 15px;
}

.work1-info {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #fff;
  left: 100%;
}

.work1-info-anim-show {
  -webkit-animation-name: absolutemove;
          animation-name: absolutemove;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.work1-info-anim-hide {
  -webkit-animation-name: absolutemove2;
          animation-name: absolutemove2;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.work1-info-img {
  max-width: 100%;
  height: 100%;
}

@-webkit-keyframes absolutemove {
  0% {
    left: 100%;
  }
  100% {
    left: 0;
  }
}

@keyframes absolutemove {
  0% {
    left: 100%;
  }
  100% {
    left: 0;
  }
}

@-webkit-keyframes absolutemove2 {
  0% {
    left: 0;
  }
  100% {
    left: 100;
  }
}

@keyframes absolutemove2 {
  0% {
    left: 0;
  }
  100% {
    left: 100;
  }
}

.work1-info-content {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  justify-content: center;
}

.work1-info-demo {
  width: 100%;
  margin-bottom: 20px;
}

.work1-info-left {
  width: 26%;
  /* min-width: 400px; */
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
}

.work1-info-tag {
  display: flex;
  flex-wrap: wrap;
}

.work1-info-tag > div {
  margin: 0 6px;
  margin-bottom: 10px;
}

.work1-info-button-container {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 15px;
  padding-left: 12px;
}

.work1-info-text-line {
  margin: 0 20px 0 5px;
}

.work1-info-right {
  width: 550px;
  min-width: 580px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 0 0 4%;
}

.work1-info-text-container {
  justify-content: center;
}
.work1-info-img-flow {
  margin-top: 20px;
  width: 100%;
}
.work1-smallh {
  font-weight: bold;
}

.work1-info-img-sus {
  margin-top: 20px;
  width: 15vw;
  max-width: 250px;
  min-width: 200px;
}

.work1-h3 {
  text-align: left;
}

.work1-info-text-container {
  justify-content: flex-start;
}

.work1-info-text-container-2 {
  justify-content: center;
}

.work1-info-text-container p {
  margin: 20px 0;
  line-height: 28px;
  align-items: center;
  text-align: left;
  font-weight: 400;
  letter-spacing: 0.075em;
  text-indent: 40px;
}

.work1-info-text-container p span {
  font-weight: bold;
}

.work1-info-back {
  position: absolute;
  left: 4%;
  top: 50%;
  /* bottom: 100px; */
  text-decoration: underline;
  cursor: pointer;
}

.work1-info-img-arrow {
  transform: rotate(-180deg);
  margin: 0px 20px 0px 0;
}

@media screen and (max-width: 1280px) {
  .work1-info-text-container p {
    margin: 10px 0;
  }
  .work1-info-img-flow {
    margin-top: 10px;
    min-width: 400px;
  }
  .work1-info-back {
    top: auto;
    bottom: 6%;
  }
}

@media screen and (max-width: 1024px) {
  .work1-main-img-m {
    display: block;
    min-height: 990px;
    min-width: 880px;
  }
  .work1-main-img-l {
    display: none;
  }
  .work1-main-content-container {
    bottom: 60px;
    width: 365px;
  }
  .work1-main-content-container::before {
    content: "";
    position: absolute;
    width: 500px;
    height: 400px;
    top: -50px;
    left: -50px;
    background-color: rgba(255, 255, 255, 0.4);
  }
  .work1-text-container {
    margin-bottom: 10px;
  }
  .work1 .work1-h3 {
    font-size: 28px;
  }
  .work1 .work1-line {
    margin-top: 1px;
    height: 30px;
  }
  .work1-main-tag-container {
    margin-bottom: 15px;
  }

  /* info */

  .work1-info-content {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px;
  }
  .work1-info-left {
    width: 80%;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: -webkit-max-content -webkit-max-content auto;
    grid-template-rows: max-content max-content auto;
    grid-column-gap: 10px;
    min-height: 200px;
    /* display: flex;
    flex-direction: column;
    height: 200px; */
  }
  .work1-info-demo {
    min-width: 100%;
    margin-bottom: 0px;
    grid-row: 1/4;
  }
  .work1-info-button-container {
    display: grid;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
    grid-template-rows: -webkit-max-content;
    grid-template-rows: max-content;
  }

  .work1-info-right {
    width: 80%;
    margin: 30px 0 0 0;
    min-width: auto;
  }
  .work1-info-text-container p {
    margin: 15px 0;
  }
  .work1-info-img-flow {
    width: 80%;
    margin: 10px auto 0;
  }
  .work1-info-back {
    position: relative;
    top: 0;
    bottom: 0;
    left: 0;
    margin-top: 5px;
  }
}

@media screen and (max-width: 768px) {
  .work1-info-left {
    width: 80%;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: auto;
    grid-template-rows: -webkit-max-content -webkit-max-content -webkit-max-content auto;
    grid-template-rows: max-content max-content max-content auto;
    min-height: auto;
  }
  .work1-info-button-container {
    margin-top: 5px;
  }
  .work1-info-right {
    margin: 4% 0 0 0;
  }
  .work1-info-demo {
    width: 70%;
    min-width: auto;
    grid-row: auto;
    justify-self: center;
    margin-bottom: 15px;
  }
  .work1-info-img-flow {
    margin: 5px auto 0;
  }
  .work1-info-img-sus {
    min-width: 180px;
    margin-top: 10px;
  }
  .work1-info-text-container p {
    margin: 10px 0;
  }
}

@media screen and (max-width: 480px) {
  .work1 {
    min-height: auto;
  }
  .work1 .work1-h3 {
    font-size: 22px;
  }
  .work1-main-content-container::before {
    display: none;
  }
  .work1-main-bg::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.4);
  }
  .work1 .work1-line {
    margin-top: 2px;
    height: 28px;
    width: 4px;
  }
  .work1-main-img-m {
    min-height: 720px;
    min-width: 700px;
  }
  .work1-main-content-container {
    bottom: 0;
    top: 40%;
  }
  .work1-info-content {
    padding-top: 0;
  }
  .work1-info-img-flow {
    display: none;
  }
  .work1-text-container {
    margin-bottom: 5px;
  }
  .work1-info-text-container p {
    margin: 2px 0;
  }
  .work1-info-right {
    margin: 8px 0 0 0;
  }
  .work1-info-left {
    width: 80%;
    grid-template-columns: auto;
    grid-template-rows: auto auto auto 40px;
  }
  .work1-info-tag > div {
    margin: 0 4px;
    margin-bottom: 5px;
  }
  .work1-info-button-container {
    margin-top: 4px;
    grid-gap: 5px;
    padding-left: 0px;
  }
  .work1-info-right {
    width: 89%;
  }
  .work1 .work1-smallh:nth-child(1) {
    display: none;
  }
  .work1-info-img-sus {
    display: none;
  }
}

.button {
  color: #fff;
  background-color: #3a3a3a;
  border-radius: 50px;
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.5));
}
.button a {
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  width: 100%;
  padding: 7px 22px 6px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.button .button-text {
  color: #fff;
  font-weight: 200;
  font-family: "Roboto", sans-serif;
}
.button-img {
  width: 13px;
  height: 13px;
  margin-left: 8px;
  margin-bottom: 1px;
}

@media screen and (max-width: 1024px) {
  .button-img {
    width: 10px;
    height: 10px;
  }
}
